.App {
    padding: 30px;
    height: 100%;
    box-sizing: border-box;
    background: #f5f5f5;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.container {
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    background: #ffffff;
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.container .form-text {
    color: #444;
}

.container .form-input {
    margin-top: 10px;
    border: 1px solid hsl(0, 0%, 80%);
    border-radius: 4px;
    font-size: 14px;
    padding: 8px;
    width: 200px;
    height: 20px;
}

.container .form-wrapper {
    display: flex;
    gap: 30px;
    margin-bottom: 40px;
}

.container .form-select {
    margin-top: 10px;
    width: 200px;
    font-size: 14px;
    border-color: #e6e6e6;
}

.container .charts-container {
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.container .chart-wrapper {
    margin-right: 80px;
}
